.CardContent {
    padding-bottom: 1%;
    width: 15%;
    min-width: 100px;
    background-color: black;

    border-radius: 3px 3px 3px 3px;

    box-shadow: 0px 0px 5px 1px bisque;

    display: flex;
    flex-direction: column;
    flex: 1;

    color: bisque;
}

a {
    color: bisque;
    text-decoration: none;
}

ul, li {
    list-style-type: none;
    list-style-position:inside;
    margin:0;
    padding:0; 
}

img {
    position: relative;

    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    width: 30%;
    padding-top: 3%;
}